/*------ Charts styles ------*/

.chart-visitors {
	min-height: 18rem;
	overflow: hidden;
}
.chart-tasks {
	height: 15rem;
	overflow: hidden;
}
.chart-donut, .chart-pie {
	height: 21rem;
	overflow: hidden;
}
.chartsh {
	height: 16rem;
	overflow: hidden;
}
.chartwidget {
	height: 17rem;
	overflow: hidden;
}
.map, .chart {
	position: relative;
	padding-top: 56.25%;
}
.map-square, .chart-square {
	padding-top: 100%;
}
.map-content, .chart-content {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}
.map-header {
	margin-bottom: 1.5rem;
	height: 15rem;
	position: relative;
	margin-bottom: -1.5rem;
	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 10rem;
		background: linear-gradient(to bottom, rgba(245, 247, 251, 0) 5%, #f1f1f9 95%);
		pointer-events: none;
	}
}
.map-header-layer {
	height: 100%;
}
.map-static {
	height: 120px;
	width: 100%;
	max-width: 640px;
	background-position: center center;
	background-size: 640px 120px;
}
.chart-circle {
	height: 7rem;
	width: 7rem;
	position: relative;
}
.chart-circle-1 {
	display: initial;
	height: 12rem;
	width: 12rem;
	position: relative;
}
.chart-circle canvas {
	margin: 0 auto;
	display: block;
	max-width: 100%;
	max-height: 100%;
}
.chart-circle-xs {
	height: 2.5rem;
	width: 2.5rem;
	font-size: .8rem;
}
.chart-circle-sm {
	height: 3rem !important;
	width: 3rem !important;
	font-size: .5rem;
}
.chart-circle-lg {
	height: 10rem;
	width: 10rem;
	font-size: .8rem;
}
.chart-circle-value {
	position: absolute;
	top: 0px;
	left: 0;
	right: 0;
	margin-left: auto;
	margin-right: auto;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-direction: column;
	flex-direction: column;
	line-height: 1;
	small {
		display: block;
		color: #9aa0ac;
		font-size: 0.9375rem;
	}
}
.chart-circle-1-value {
	position: absolute;
	top: -184px;
	left: 0;
	right: 0;
	margin-left: auto;
	margin-right: auto;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-direction: column;
	flex-direction: column;
	line-height: 1;
	font-size: 40px;
	small {
		display: block;
		color: #9aa0ac;
		font-size: 0.9375rem;
	}
}
.chart-circle-value-3 {
	position: absolute;
	top: 20px;
	left: 0;
	right: 0;
	margin-left: auto;
	margin-right: auto;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-direction: column;
	flex-direction: column;
	line-height: 1;
}
.chart-circle-value-4 {
	position: absolute;
	top: -66px;
	left: 0;
	right: 0;
	margin-left: auto;
	margin-right: auto;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-direction: column;
	flex-direction: column;
	line-height: 1;
	font-size: 50px;
}
.amcharts-chart-div a, .canvasjs-chart-container a {
	display: none !important;
}
.amChartsLegend.amcharts-legend-div {
	display: none;
}
/*----chart-drop-shadow----*/

.chart-dropshadow {
	-webkit-filter: drop-shadow((-6px) 12px 4px $black-1);
	filter: drop-shadow((-6px) 12px 4px $black-1);
}
.chart-dropshadow-primary {
	-webkit-filter: drop-shadow((-6px) 12px 4px rgba(133, 67, 246, 0.5));
	filter: drop-shadow((-6px) 12px 4px rgba(133, 67, 246, 0.5));
}
.chart-dropshadow-primary-1 {
	-webkit-filter: drop-shadow((-6px) 12px 4px rgba(133, 67, 246, 0.2));
	filter: drop-shadow((-6px) 12px 4px rgba(133, 67, 246, 0.2));
}
.chart-dropshadow-danger {
	-webkit-filter: drop-shadow((-6px) 12px 4px rgba(244, 88, 91, 0.5));
	filter: drop-shadow((-6px) 12px 4px rgba(244, 88, 91, 0.5));
}
.chart-dropshadow-warning {
	-webkit-filter: drop-shadow((-6px) 12px 4px rgba(251, 141, 52, 0.5));
	filter: drop-shadow((-6px) 12px 4px rgba(251, 141, 52, 0.5));
}
.BarChartShadow {
	-webkit-filter: drop-shadow((-4px) 9px 4px $black-3);
	filter: drop-shadow((-6px) 9px 4px $black-3);
}
.highcharts-credits, .highcharts-button, .highcharts-data-label, .highcharts-label {
	display: none;
}
.highcharts-data-label-connector {
	display: none !important;
}
#areaChart1, #areaChart2, #areaChart3, #areaChart4 {
	height: 7rem !important;
}
#AreaChart5 {
	width: 105% !important;
	height: 163px !important;
	bottom: -15px;
	position: relative;
	left: -7px;
}
/*----chart-drop-shadow----*/

.chart-dropshadow2 {
	-webkit-filter: drop-shadow((-6px) 5px 4px $black-2);
	filter: drop-shadow((-6px) 5px 4px $black-2);
}
.chart-dropshadow-secondary {
	-webkit-filter: drop-shadow((-6px) 5px 4px rgba(167, 66, 153, 0.2));
	filter: drop-shadow((-6px) 5px 4px rgba(167, 66, 153, 0.2));
}
.chart-dropshadow-success {
	-webkit-filter: drop-shadow((-6px) 5px 4px rgba(19, 191, 166, 0.2));
	filter: drop-shadow((-6px) 5px 4px rgba(19, 191, 166, 0.2));
}
.chart-dropshadow-info {
	-webkit-filter: drop-shadow((-6px) 5px 4px rgba(7, 116, 248, 0.2));
	filter: drop-shadow((-6px) 5px 4px rgba(7, 116, 248, 0.2));
}
.app.boxed {
	#revenue.areaChart.chartjs-render-monitor {
		width: 100% !important;
		overflow: hidden !important;
	}
	#doughutChart.donutShadow.chartjs-render-monitor {
		width: 150% !important;
		bottom: -15px;
		position: relative;
		right: -60px;
		overflow: hidden !important;
	}
	#revenue.chartjs-render-monitor {
		width: 100% !important;
		overflow: hidden !important;
	}
	#morrisBar8 svg {
		width: 100% !important;
		right: 130px !important;
		overflow: inherit !important;
	}
}
.sparkline_area {
	position: relative;
	bottom: -3px;
}