.richText {
	.richText-toolbar {
		ul li a {
		    border-left: rgba(156, 162, 161, 0.2) solid 1px;
		}
		border-bottom: rgba(156, 162, 161, 0.1) solid 1px;
	}
	border: #eaedf1 solid 1px;
	background-color: #f1f1f9;
}
.cal1 {
	.clndr {
		.clndr-table {
			.header-days .header-day {
			    border-left: 1px solid $white-1;
				border-top: 1px solid #e2e3e4;
			}
			tr {
				.empty, .adjacent-month, .my-empty, .my-adjacent-month {
				    border-left: 1px solid #eaedf1;
					border-top: 1px solid #eaedf1;
					background: #f1f1f9;
				}
				.day {
					&.event, &.my-event {
						background: #f1f1f9;
					}
				    border-left: 1px solid #eaedf1;
					border-top: 1px solid #eaedf1;
				    border-right: 1px solid #eaedf1;
					&:last-child {
					    border-right: 1px solid #eaedf1;
					}
					&:hover {
						background: #fcfaff;
					}
				}
			}
		}
		.clndr-controls .clndr-control-button {
			.clndr-previous-button, .clndr-next-button {
				color: $white;
			}
		}
	}
	font-size: 14px;
}
.fc-unthemed {
	.fc-content, .fc-divider, .fc-list-heading td, .fc-list-view, .fc-popover, .fc-row, tbody, td, th, thead {
		border-color: #eaedf1;
	}
}
.fc-event, .fc-event-dot {
	color: #000 !important;
	padding: 5px !important;
}
.fc-unthemed {
	.fc-divider, .fc-list-heading td, .fc-popover .fc-header {
		background: #e2e3e4;
	}
}
.fc-toolbar {
	.fc-state-active, .ui-state-active {
		background: #b4b4b4;
	}
}
.fc-today-button fc-button fc-state-default fc-corner-left fc-corner-right fc-state-disabled:focus {
	border: none !important;
	box-shadow: none !important;
}
.fc-unthemed .fc-list-item:hover td {
	background-color: #eeeeee;
}
.conv-form-wrapper div#messages div.message.to {
	background: #f1f1f9;
}
